import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import HttpApi from 'i18next-http-backend';

i18n
  .use(HttpApi) 
  .use(initReactI18next)
  .init({
    supportedLngs: ['it', 'en'],
    fallbackLng: 'it',
    debug: true,
    backend: {
      loadPath: 'locales/{{lng}}/translation.json',
    },
    react: {
      useSuspense: false, 
    },
  });

export default i18n;
